import { reactive, toRefs } from "vue";
import {loginServicio} from "@/services/loginServicio"
import { userTokenStore } from '@/store/tokenStore'
import { useRouter } from "vue-router";

export default {
  name: "LoginComponent",
  props: [],
  components: {
  },
  setup() {
    const form = reactive({
      email: "",
      password: "",
      confirmPassword: "",
    });
    const router = useRouter();
    const store = userTokenStore();
    function login(){
      loginServicio(form).then((result) => {
        if (result.status == 200) {
          form.confirmPassword = "Contraseña Correcta"

          store.setToken(result.data)
          localStorage.token=result.data;
          if (store.getTokenData.rol!= 'admin') {
            router.push({name: 'client'});
          }else{
            router.push({name: 'admin'});
          }
        }else{
          form.confirmPassword = "Contraseña InCorrecta"
        }
      });
    }
    return {
      ...toRefs(form),
      login,
      store
    };
  },
};
